body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1240px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

.rangeslider,
.rangeslider__fill {
  display: block;
  -moz-box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.3);
  box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.3);
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

.rangeslider {
  background: #e6e6e6;
  position: relative;
}

.rangeslider--horizontal {
  height: 20px;
  width: 100%;
}

.rangeslider--vertical {
  width: 20px;
  min-height: 150px;
  max-height: 100%;
}

.rangeslider--disabled {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
  opacity: 0.4;
}

.rangeslider__fill {
  background: #00ff00;
  position: absolute;
}

.rangeslider--horizontal .rangeslider__fill {
  top: 0;
  height: 100%;
}

.rangeslider--vertical .rangeslider__fill {
  bottom: 0;
  width: 100%;
}

.rangeslider__handle {
  background: white;
  border: 1px solid #ccc;
  cursor: pointer;
  display: inline-block;
  width: 40px;
  height: 40px;
  position: absolute;
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: -webkit-gradient(linear, 50% 0%, 50% 100%, color-stop(0%, rgba(255, 255, 255, 0)), color-stop(100%, rgba(0, 0, 0, 0.1)));
  background-image: -moz-linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.1));
  background-image: -webkit-linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.1));
  background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.1));
  -moz-box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

.rangeslider__handle:after {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  margin: auto;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: -webkit-gradient(linear, 50% 0%, 50% 100%, color-stop(0%, rgba(0, 0, 0, 0.13)), color-stop(100%, rgba(255, 255, 255, 0)));
  background-image: -moz-linear-gradient(rgba(0, 0, 0, 0.13), rgba(255, 255, 255, 0));
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.13), rgba(255, 255, 255, 0));
  background-image: linear-gradient(rgba(0, 0, 0, 0.13), rgba(255, 255, 255, 0));
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

.rangeslider__handle:active,
.rangeslider--active .rangeslider__handle {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: -webkit-gradient(linear, 50% 0%, 50% 100%, color-stop(0%, rgba(0, 0, 0, 0.1)), color-stop(100%, rgba(0, 0, 0, 0.12)));
  background-image: -moz-linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.12));
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.12));
  background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.12));
}

.rangeslider--horizontal .rangeslider__handle {
  top: -10px;
  touch-action: pan-y;
  -ms-touch-action: pan-y;
}

.rangeslider--vertical .rangeslider__handle {
  left: -10px;
  touch-action: pan-x;
  -ms-touch-action: pan-x;
}

input[type="range"]:focus + .rangeslider .rangeslider__handle {
  -moz-box-shadow: 0 0 8px rgba(255, 0, 255, 0.9);
  -webkit-box-shadow: 0 0 8px rgba(255, 0, 255, 0.9);
  box-shadow: 0 0 8px rgba(255, 0, 255, 0.9);
}

html,
body {
  width: 100%;
  height: 100%;
  background-color: #111;
}

body {
  font-family: 'Oswald', 'Arial', sans-serif;
  font-size: 100%;
  font-weight: 300;
  font-style: normal;
  line-height: 40px;
  color: #fff;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.highlight {
  color: #991d4e;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 4px 24px;
  font-family: inherit;
  font-size: 25px;
  font-weight: 300;
  text-align: center;
  text-decoration: none;
  color: #fff;
  border: 1px solid #333;
  background-color: #333;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn:hover,
.btn:active {
  background-color: #7b133c;
  border-color: #7b133c;
}

.btn:focus {
  outline: none;
  border-color: #7b133c;
}

.page-header {
  min-height: 450px;
  padding: 70px 0;
}

.page-header .flex-container {
  display: flex;
  flex-flow: row wrap;
}

.page-header__text {
  max-width: 800px;
  min-width: 0;
}

.page-header__title {
  margin-bottom: 40px;
  font-size: 3.75em;
  line-height: 78px;
  font-weight: 300;
  text-transform: uppercase;
  word-wrap: break-word;
}

.page-header__title b {
  font-weight: 600;
}

.page-header__subtitle {
  margin-bottom: 60px;
  font-size: 1.875em;
}

.page-header__subtitle b {
  font-weight: 500;
}

.page-header__desc {
  font-size: 1.875em;
}

.logo {
  width: 148px;
  height: 148px;
  margin-left: auto;
}

.brief {
  min-height: 100vh;
}

.brief .wrapper {
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
}

.brief-slide {
  display: none;
}

.brief-slide__title {
  position: relative;
  max-width: 450px;
  margin-bottom: 60px;
}

.brief-slide__title h2 {
  position: relative;
  padding: 16px 0;
  font-size: 2.1875em;
  font-weight: 400;
  z-index: 5;
}

.brief-slide__title-bkg {
  position: absolute;
  top: 0;
  right: 0;
  width: 9999px;
  height: 100%;
  background-color: #7b133c;
  z-index: 1;
}

.form__items .flex-container {
  position: relative;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-left: -20px;
  margin-right: -20px;
}

.form__item {
  width: 42.3387%;
  /* from 1240px */
  min-width: 280px;
  margin-left: 20px;
  margin-right: 20px;
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;
}

.form__item-title {
  display: block;
  margin-bottom: 20px;
  font-size: 1.5625em;
}

.form__field {
  min-height: 200px;
  padding: 7px 12px;
  font-size: 1.25em;
  line-height: 1.2;
  letter-spacing: 0.05em;
  color: inherit;
  background-color: #333;
  border: 1px solid #333;
  outline: none;
}

.form__field:focus {
  border: 1px solid #991d4e;
}

.form__field--days {
  min-height: 34px;
  width: 95px;
  text-align: center;
  overflow: hidden;
}

.form__field--input {
  min-height: 34px;
}

textarea.form__field,
.input-items {
  margin-top: auto;
}

/*
[placeholder] {
  &::-webkit-input-placeholder {
    color: white;
  }
  &::-moz-placeholder {
    color: white;
  }
  &:-ms-input-placeholder {
    color: white;
  }
  &:-moz-placeholder {
    color: white;
  }
}*/

.input-items--big-stack,
textarea.form__field--small-mt {
  margin-top: 20px;
}

.input-item {
  position: relative;
  margin-bottom: 55px;
}

.input-item:last-of-type {
  margin-bottom: 0;
}

.input-item__text {
  padding-left: 60px;
  font-size: 1.5625em;
  text-transform: uppercase;
}

.input-item__text--paragraph {
  text-transform: none;
}

.input-item__radio {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.input-item__indicator {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border: 8px solid #333;
  background-color: #333;
  cursor: pointer;
}

.input-item__indicator--radio {
  border-radius: 50%;
}

.input-item__radio:checked + .input-item__indicator {
  background-color: #7b133c;
}

.input-item__field {
  position: relative;
  bottom: 5px;
  width: 74.4%;
  padding: 7px 12px;
  border: 0;
  border-bottom: 1px solid #7b133c;
  font-family: inherit;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: 0.05em;
  color: inherit;
  background-color: transparent;
  outline: none;
}

.input-item__field:focus {
  background-color: #333;
}

.sup {
  display: inline-block;
  margin-left: 16px;
  line-height: 1;
  font-size: 1.0625em;
  vertical-align: top;
}

.range {
  position: relative;
  margin-top: 30px;
}

.rangeslider {
  background: #333;
}

.rangeslider--horizontal {
  height: 4px;
}

.rangeslider__fill {
  background: #7b133c;
}

.rangeslider__handle {
  width: 20px;
  height: 20px;
  border: 0;
  background: #7b133c;
  box-shadow: none;
}

.rangeslider__handle::after {
  content: none;
}

.rangeslider--horizontal .rangeslider__handle {
  top: -8px;
}

.title-min,
.title-max {
  position: absolute;
  top: 18px;
  font-size: 15px;
  line-height: 20px;
}

.title-max {
  right: 0;
  text-align: right;
}

.slider__controls-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  width: 43.75%;
  /* from 1200px */
  margin-left: auto;
}

.slider-controls--prev,
.slider-controls--next {
  width: 48%;
  min-width: 48px;
}

.require::after {
  content: "*";
  font-size: inherit;
  color: #991d4e;
}

.btn[name="feedback"] {
  display: block;
  width: 100%;
  margin-top: 16px;
  visibility: hidden;
}

.active {
  display: block;
  transition: display 0.8s ease;
}

@media (max-width: 1004px) {
  .logo {
    order: -1;
    margin-right: auto;
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .brief .wrapper {
    padding-top: 50px;
  }

  .form__item-title {
    font-size: 1.25em;
    line-height: 1.3;
  }

  .input-item {
    margin-bottom: 30px;
  }

  .input-item__text {
    font-size: 1.25em;
  }
}

@media screen and (max-width: 768px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .page-header__title {
    margin-bottom: 30px;
    font-size: 3.5em;
    line-height: 1.3;
  }

  .page-header__subtitle {
    margin-bottom: 30px;
    font-size: 1.75em;
    line-height: 1.3;
  }

  .page-header__desc {
    font-size: 1.75em;
    line-height: 1.3;
  }

  .brief-slide__title {
    margin-bottom: 40px;
  }

  .brief-slide__title h2 {
    font-size: 1.875em;
  }

  .form__items .flex-container {
    margin-left: -15px;
    margin-right: -15px;
  }

  .form__item {
    margin-left: 15px;
    margin-right: 15px;
  }

  .input-item__field {
    position: static;
    bottom: 0;
    display: block;
    width: 100%;
    margin-top: 10px;
  }

  .slider__controls-container {
    width: 80%;
    margin-right: auto;
  }
}

@media (max-width: 634px) {
  .form__items .flex-container {
    justify-content: center;
  }

  .form__item {
    width: 80%;
  }
}

@media screen and (max-width: 544px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .page-header {
    padding: 50px 0;
  }
}

@media screen and (max-width: 420px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .page-header__title {
    font-size: 2.75em;
  }

  .page-header__subtitle {
    font-size: 1.5625em;
  }

  .page-header__desc {
    font-size: 1.5625em;
  }
}